/*
* square.js
*
* Este ficheiro contem uma classe para os quadrados do tabuleiro de xadrez em JavaScript.
*
* A classe possui um construtor que define as seguintes propriedades: x, y, canvascoord e pieceOnThisSquare.
* A classe tambm define O mtodo: setPiece.
* O mtodo setPiece que recebe um argumento newPiece permite atribuir uma pea a um quadrado.
*/

class Square {
    constructor(x, y, pieceOnThisSquare, canvasCoord) {
        // x  uma varivel de tipo int que vai guardar a coordenada x: 0 < x < 7  do quadrado.
        this.x = x
        // y  uma varivel de tipo int que vai guardar a coordenada y: 0 < y < 7  do quadrado.
        this.y = y 
        // canvasCoord  uma varivel que vai guardar a coordenada no tabuleiro.
        this.canvasCoord = canvasCoord
        // pieceOnThisSquare  uma varivel que que permite saber se uma pea esta sobre este quadrado.
        this.pieceOnThisSquare = pieceOnThisSquare
    }

    setPiece(newPiece) {
        /*
        * setPiece recebe um argumento newPiece.
        * Este mtodo  usado para atribuir uma pea a um quadrado, remover uma pea de um quadrado ou substituir a pea num quadrado por uma pea diferente.
        * 
        * Dependendo dos valores de newPiece e da pea atual no quadrado, existem diferentes casos para o que a funo faz.
        * Se newPiece no for nulo e houver pea no quadrado, e as peas forem de cores diferentes, a funo substitui a pea no quadrado por newPiece e define a posio de newPiece como o quadrado atual.
        * Se as peas forem da mesma cor, a funo retorna a string "user tried to capture their own piece".
        */

        // Se newPiece for nulo e no houver pea no quadrado, a funo no faz nada.
        if (newPiece === null && this.pieceOnThisSquare === null) {
            return
        } else if (newPiece === null) {
            // (caso em que o chamador da funo deseja remover a pea que est neste quadrado.) 
            // Se newPiece for nulo e houver pea no quadrado, a funo remove a pea do quadrado e define sua posio como indefinida.
            this.pieceOnThisSquare.setSquare(undefined)
            this.pieceOnThisSquare = null
        } else if (this.pieceOnThisSquare === null) {
            // Se newPiece no for nulo e no houver pea no quadrado, a funo atribui newPiece ao quadrado e define a posio de newPiece como o quadrado atual.
            this.pieceOnThisSquare = newPiece
            newPiece.setSquare(this)
        } else if (this.getPieceIdOnThisSquare() !== newPiece.id && this.pieceOnThisSquare.color !== newPiece.color) {
            // (Captura)
            // Caso em que o chamador da funo deseja alterar a pea neste quadrado. (somente cores diferentes so permitidas).
            this.pieceOnThisSquare = newPiece
            newPiece.setSquare(this)
        } else {
            // Caso em que o jogador tento capturar a sua prpria pea.
            return "user tried to capture their own piece"
        }
    }

    removePiece() {
        // Remover pea deste quadrado
        this.pieceOnThisSquare = null
    }

    getPiece() {
        // Obter a pea que esta neste quadrado
        return this.pieceOnThisSquare 
    }

    getPieceIdOnThisSquare() {
        // Obter o nmero de identificao da pea que esta neste quadrado
        // Retorne "empty" se no estiver nenhuma pea no quadrado
        if (this.pieceOnThisSquare === null) {
            return "empty"
        }

        return this.pieceOnThisSquare.id
    }

    isOccupied() {
        // Esta funo verifica se o quadrado atual est ocupado por uma pea ou no.
        return this.pieceOnThisSquare != null
    }

    getCoord() {
        // Obtm as coordonadas da pea  
        return [this.x, this.y]
    }

    getCanvasCoord() {
        // Obtm as coordonadas no tabuleiro  
        return this.canvasCoord
    }
}

// export para poder utilizar a classe "Square" nos outros ficheiros
export default Square