// piece.js

// import necessrios para este ficheiro
import React from 'react'
import { Image } from 'react-konva';
import useImage from 'use-image'

const Piece = (props) => {
    /* isto  um componente React que exibe uma pea de xadrez no tabuleiro. O componente recebe o componente "props"
    *
    * "props.isWhite" determina a cor da pea (branco ou preto).
    * "props.imgurls"  um array que contm as URLs das imagens das peas brancas e pretas.
    * "props.id"  o identificador nico da pea.
    * "props.draggedPieceTargetId"  o id da pea que est a ser arrastada no momento.
    * "props.thisPlayersColorIsWhite" indica a cor das peas que o jogador est a utilisar.
    * "props.playerTurnToMoveIsWhite" indica a cor das peas que podem fazer um movimento.
    * "props.whiteKingInCheck" e "props.blackKingInCheck" indicam se os reis branco e preto esto em xeque, respetivamente.
    * 
    * Usa a biblioteca "useImage" para importar uma imagem com base na propriedade "choiceOfColor".
    * Usa o componente "Image" da biblioteca "react-konva" para exibir a pea de xadrez no tabuleiro.
    * A pea  arrastvel se for a vez do jogador que est a usar peas dessa cor e se a pea que est a ser exibida for da mesma cor das peas do jogador.
    * 
    * Tambm altera a largura e a altura da pea se ela estiver arrastada.
    * Define a propriedade "fill" como vermelha se o rei da mesma cor estiver em xeque.
    * Define a propriedade "id" para o identificador exclusivo da pea.
    */ 
   
    const choiceOfColor = props.isWhite ? 0 : 1
    const [image] = useImage(props.imgurls[choiceOfColor]);
    const isDragged = props.id === props.draggedPieceTargetId

    const canThisPieceEvenBeMovedByThisPlayer = props.isWhite === props.thisPlayersColorIsWhite
    const isItThatPlayersTurn = props.playerTurnToMoveIsWhite === props.thisPlayersColorIsWhite

    const thisWhiteKingInCheck = props.id === "wk1" && props.whiteKingInCheck
    const thisBlackKingInCheck = props.id === "bk1" && props.blackKingInCheck

    // Retorna as propriedades da pea.
    return <Image image={image}
         x = {props.x - 90}
         y = {props.y - 90}
         draggable = {canThisPieceEvenBeMovedByThisPlayer && isItThatPlayersTurn}
         width = {isDragged ? 75 : 60}
         height = {isDragged ? 75 : 60}
         onDragStart = {props.onDragStart}
         onDragEnd = {props.onDragEnd}
         fill = {(thisWhiteKingInCheck && "red") || (thisBlackKingInCheck && "red")}
         id = {props.id}
         />;
};

// export para poder utilizar nos outros ficheiros
export default Piece

