/*
* chesspiece.js
*
* Este ficheiro contem uma classe para as peas de xadrez em JavaScript.
* 
* A classe possui um construtor que recebe como entrada as seguintes propriedades: name, isAttacked, color e id.
* A classe tambm define dois mtodos: setSquare e getSquare.
*
* O mtodo setSquare permite definir o quadrado em que a pea est usando o mtodo setPiece do quadrado.
* O mtodo getSquare retorna o quadrado em que a pea est.
*/

class ChessPiece {
    constructor(name, isAttacked, color, id) {
        // name  uma varivel de tipo string que vai guardar o nome da pea.
        this.name = name
        // isAttacked  uma varivel Boolean para saber se a pea esta a ser atacada (1 = atacado, 0 = no atacado).
        this.isAttacked = isAttacked 
        // color  uma varivel de tipo string que vai guardar a cor da pea.
        this.color = color
        // id  uma varivel de tipo string que vai guardar o numero de identificao da pea 
        this.id = id
    }

    setSquare(newSquare) {
        /*
        * O mtodo setSquare leva um argumento newSquare que representa o novo quadrado no qual a pea ser colocada.
        *
        * Se newSquare for indefinido, o mtodo retornar imediatamente sem fazer alteraes.
        * 
        */

        // Se newSquare for indefinido, define a propriedade squareThisPieceIsOn do objeto como newSquare. 
        if (newSquare === undefined) {
            this.squareThisPieceIsOn = newSquare
            return 
        }

        // Se squareThisPieceIsOn for indefinido ou diferente de newSquare, ele definir a propriedade
        // squareThisPieceIsOn como newSquare e chamar o mtodo setPiece em newSquare com "this" como argumento.
        if (this.squareThisPieceIsOn === undefined) {
            this.squareThisPieceIsOn = newSquare
            newSquare.setPiece(this)
        }

        const isNewSquareDifferent = this.squareThisPieceIsOn.x !== newSquare.x || this.squareThisPieceIsOn.y !== newSquare.y

        if (isNewSquareDifferent) {
            this.squareThisPieceIsOn = newSquare
            newSquare.setPiece(this)
        }
    }

    getSquare() {
        // obter o quadrado no qual a pea esta atualmente
        return this.squareThisPieceIsOn
    }
}

// export para poder utilizar a classe ChessPiece nos outros ficheiros
export default ChessPiece